const initialState = {
    data: [],
};

const todos = (state = initialState, action) => {
    switch (action.type) {
        case "ADD_TODO": // check for ADD_TODO case
            return {
                ...state,
                data: [
                    ...state.data,
                    {
                        message: action.message,
                        id: action.id,
                    },
                ],
            };

        case "DELETE_TODO":
            const todos = state.data.filter((todo) => todo.id !== action.id);
            return {
                ...state,
                data: todos,
            };
        default:
            return state;
    }
};

export default todos;