
//for ADD Action
const ADD_TODO = 'ADD_TODO';
export const addTodo = (message) => ({
    type: ADD_TODO, //check type it is ADD_TODO, It will go to reducer/todos.js
    message,
    id: Math.random(),
});

//for DELETE Action
const DELETE_TODO = 'DELETE_TODO'
export const deleteTodo = (id) => ({
    type: DELETE_TODO,
    id,
});